.container {
  display: block;
  max-width: 1670px;
  margin: auto;
  max-height: 100%; }
  @media (min-width: 768px) {
    .container {
      width: 725px; } }
  @media (min-width: 992px) {
    .container {
      width: 945px; } }
  @media (min-width: 1200px) {
    .container {
      width: 1145px; } }
  @media (min-width: 1300px) {
    .container {
      width: 1270px; } }
  @media (min-width: 1400px) {
    .container {
      width: 1370px; } }
  @media (min-width: 1500px) {
    .container {
      width: 1470px; } }
  @media (min-width: 1700px) {
    .container {
      width: 1670px; } }

.row {
  display: flex;
  max-width: 100%;
  max-height: 100%;
  width: 100%; }
